import React, { useEffect, useState } from 'react';
import { Nav } from 'react-bootstrap'
import { NavLink, Route } from 'react-router-dom';
import style from './Admin.module.css'
import { Row, Col, Table } from 'react-bootstrap'
import firebase from '../../firebaseConfig';

export default function Admin(props) {
    return (
        <>
            <div className={style.container}>
                <div className={style.menu}>
                    <Nav className="flex-column"  >
                        <Nav.Item>
                            <NavLink className="nav-link" to={"/admin/products"} >Products</NavLink>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="link-2">Option 2</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="link-3">Option 3</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="link-4">Option 4</Nav.Link>
                        </Nav.Item>
                    </Nav>
                </div>
                <div className={style.body}>
                    <Route exact path='/admin/products' render={() => <Products />} />
                </div>
            </div>
        </>
    )
}

const Products = (props) => {
    const [brendName, setBrendName] = useState('')
    const [type, setType] = useState('')
    const [sizeWheel, setSizeWheel] = useState('')
    const [sizeFrame, setSizeFrame] = useState('')
    const [speedsNumber, setSpeedsNumber] = useState('')
    const [price, setPrice] = useState('')
    const [productImage, setProductImage] = useState('')
    const [products, setProducts] = useState([])
    // const products = [
    //     { brendName, type, sizeWheel, sizeFrame, speedsNumber,price },
    //     { brendName, type, sizeWheel, sizeFrame, speedsNumber,price },
    //     { brendName, type, sizeWheel, sizeFrame, speedsNumber,price },
    // ]
    // useEffect(firebase.firestore().collection("product").get().then((querySnapshot) => {
    //     querySnapshot.forEach((doc) => {
    //         console.log(`${doc.id} => ${doc.data()}`);
    //     });
    // })
    // )
    // setProducts([
    //         { brendName, type, sizeWheel, sizeFrame, speedsNumber,price },
    //         { brendName, type, sizeWheel, sizeFrame, speedsNumber,price },
    //         { brendName, type, sizeWheel, sizeFrame, speedsNumber,price },
    //     ]
    //     )
    const add = (e) => {
        e.preventDefault();
        firebase.firestore().collection("product").add({
            brendName: brendName,
            type: type,
            price: price,
            sizeFrame: sizeFrame,
            sizeWheel: sizeWheel,
            speedsNumber: speedsNumber,
            dateAdd: new Date()
        })
            .then((docRef) => {
                setBrendName('')
                setType('')
                setSizeWheel('')
                setSizeFrame('')
                setSpeedsNumber('')
                setPrice('')
                console.log("Document written with ID: ", docRef.id);
            })
            .catch((error) => {
                console.error("Error adding document: ", error);
            });
    }

    return (
        <>
            <form >
                <div className="form-group">
                    <label>Brend name</label>
                    <input className="form-control"
                        name={'brendName'}
                        placeholder={'Enter brend name'}
                        type="text"
                        value={brendName}
                        onChange={(e) => { setBrendName(e.currentTarget.value) }} />
                </div>
                <div className="form-group">
                    <label>Type name</label>
                    <input className="form-control"
                        name={'type'}
                        placeholder={'Enter Type name'}
                        type="text"
                        value={type}
                        onChange={(e) => { setType(e.currentTarget.value) }} />
                </div>
                <Row>
                    <Col>
                        <div className="form-group">
                            <label>Size Wheel</label>
                            <input className="form-control"
                                name={'sizeWheel'}
                                placeholder={'Enter Size Wheel'}
                                type="text"
                                value={sizeWheel}
                                onChange={(e) => { setSizeWheel(e.currentTarget.value) }} />
                        </div>
                    </Col>
                    <Col>
                        <div className="form-group">
                            <label>Size Frame</label>
                            <input className="form-control"
                                name={'sizeFrame'}
                                placeholder={'Enter Size Frame'}
                                type="text"
                                value={sizeFrame}
                                onChange={(e) => { setSizeFrame(e.currentTarget.value) }} />
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className="form-group">
                            <label>Speeds Number</label>
                            <input className="form-control"
                                name={'speedsNumber'}
                                placeholder={'Enter Speeds Number'}
                                type="text"
                                value={speedsNumber}
                                onChange={(e) => { setSpeedsNumber(e.currentTarget.value) }} />
                        </div>
                    </Col>
                    <Col>
                        <div className="form-group">
                            <label>Price</label>
                            <input className="form-control"
                                name={'price'}
                                placeholder={'Enter Price'}
                                type="text"
                                value={price}
                                onChange={(e) => { setPrice(e.currentTarget.value) }} />
                        </div>
                    </Col>
                </Row>
                <button type="submit" className="btn btn-dark btn-lg btn-block" onClick={add}>Submit</button>
            </form >
            <Table bordered hover variant="dark" >
                <thead>
                    <tr>
                        <th>brendName</th>
                        <th>type</th>
                        <th>sizeWheel</th>
                        <th>sizeFrame</th>
                        <th>speedsNumber</th>
                        <th>price</th>
                    </tr>
                </thead>
                <tbody>
                    {products.map(p =>
                        <tr>
                            <td>{p.brendName}</td>
                            <td>{p.type}</td>
                            <td>{p.sizeWheel}</td>
                            <td>{p.sizeFrame}</td>
                            <td>{p.speedsNumber}</td>
                            <td>{p.price}</td>
                        </tr>)
                    }


                </tbody>
            </Table>
        </>
    )
}
