// import firebase from 'firebase';
import firebase from '../firebaseConfig';
import { stopSubmit } from 'redux-form';

const SET_BREND_NAME = 'product/SET_BREND_NAME'
const SET_TYPE = 'product/SET_TYPE'
const SET_PRICE = 'product/SET_PRICE'
const SET_SIZE_FRAME = 'product/SET_SIZE_FRAME'
const SET_SIZE_WHEEL = 'product/SET_SIZE_WHEEL'
const SET_SPEEDS_NUMBER = 'product/SET_SPEEDS_NUMBER'


let initialState = {
    brendName: null,
    type: null,
    price: null,
    sizeFrame: null,
    sizeWheel: null,
    speedsNumber: null,
}


const productReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_BREND_NAME:
            return {
                ...state,
                ...action.payload,
            }
        case SET_TYPE:
            return {
                ...state,
                ...action.payload,
            }
        case SET_PRICE:
            return {
                ...state,
                ...action.payload,
            }
        case SET_SIZE_FRAME:
            return {
                ...state,
                ...action.payload,
            }
        case SET_SIZE_WHEEL:
            return {
                ...state,
                ...action.payload,
            }
        case SET_SPEEDS_NUMBER:
            return {
                ...state,
                ...action.payload,
            }
        default:
            return state;
    }
}

export const setBrendName = (brendName) => ({ type: SET_BREND_NAME, payload: { brendName } })
export const setType = (type) => ({ type: SET_TYPE, payload: { type } })
export const setPrice = (price) => ({ type: SET_PRICE, payload: { price } })
export const setSizeFrame = (sizeFrame) => ({ type: SET_SIZE_FRAME, payload: { sizeFrame } })
export const setSizeWheel = (sizeWheel) => ({ type: SET_SIZE_WHEEL, payload: { sizeWheel } })
export const setSpeedsNumber = (speedsNumber) => ({ type: SET_SPEEDS_NUMBER, payload: { speedsNumber } })

export const addProductFirestore = (brendName, type, price, sizeFrame, sizeWheel, speedsNumber) => {
    firebase.firestore().collection("product").add({
        brendName: brendName,
        type: type,
        price: price,
        sizeFrame: sizeFrame,
        sizeWheel: sizeWheel,
        speedsNumber: speedsNumber,
    })
        .then((docRef) => {
            console.log("Document written with ID: ", docRef.id);
        })
        .catch((error) => {
            console.error("Error adding document: ", error);
        });
}
export default productReducer